.App {
  text-align: center;
  width: 200vw;
  height: 100vh;
  justify-content: center;
  flex-direction: column;
}

body {
  height: 100vh;
  padding:0;
  margin:0;
  overflow-x: scroll;
  overflow-y: hidden;
  background: #282c34;
}

.App-header {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: white;
}

.inner {
  overflow: hidden;
  width: 200vw;
}

.container-custom {
  width: 100vw;
  display: block;
  float: left;
}

.about-background {
  background-color: rgba(255, 255, 255, 0.5);
}

a {
  color: inherit;
}

a:hover {
  color: inherit;
}

.button {
  border-radius: 4px;
  background: linear-gradient(190deg, #ffcbff, #df61ff);
  color: #FFFFFF;
  padding: 0.7em 1.2em;
  transition: all 0.5s;
  cursor: pointer;
  float: right;
}

.button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.button span:after {
  content: '\00bb';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.button:hover span {
  padding-right: 25px;
}

.button:hover span:after {
  opacity: 1;
  right: 0;
}


@keyframes fadein1 {
  0% { opacity: 0; }
  25%   { opacity: 1; }
}

@keyframes fadein2 {
  0% { opacity: 0; }
  25% { opacity: 0; }
  50%  { opacity: 1; }
}

@keyframes fadein3 {
  0% { opacity: 0; }
  25% { opacity: 0; }
  50% { opacity: 0; }
  75%   { opacity: 1; }
}

@keyframes fadein4 {
  0% { opacity: 0; }
  25% { opacity: 0; }
  50% { opacity: 0; }
  75% { opacity: 0; }
  100%   { opacity: 1; }
}

@keyframes fadeout {
  0% { 
    opacity: 1; 
    transform: translateX(0);
  }
  100% { 
    opacity: 0;
    transform: translateX(-20px); 
  }
}


/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 24px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgb(177, 177, 177);
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 24px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 4px;
  bottom: 4px;
  background-color: rgb(255, 198, 246);
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 50%;
}


input:checked + .slider {
  background-color: rgb(68, 68, 68);
  color: black;
}


input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}